import PropTypes from "prop-types";
import { useState } from "react";
import { PDFDownloadLink, PDFViewer } from "@react-pdf/renderer";
// material
import {
  Box,
  Tooltip,
  IconButton,
  DialogActions,
  Stack,
  Button,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
//
import { DialogAnimate } from "../../../../components/animate";
import Iconify from "../../../../components/Iconify";
import InvoicePDF from "./InvoicePDF";

import useAuth from "../../../../hooks/useAuth";
import { fDate } from "../../../../utils/formatTime";
import { useTranslation } from "react-i18next";

// ----------------------------------------------------------------------

InvoiceToolbar.propTypes = {
  reports: PropTypes.array,
  filterUser: PropTypes.string,
  filterEndDate: PropTypes.instanceOf(Date),
  filterStartDate: PropTypes.instanceOf(Date),
  onFilterEndDate: PropTypes.func,
  onFilterUser: PropTypes.func,
  onFilterStartDate: PropTypes.func,
  users: PropTypes.array,
  handleRefresh: PropTypes.func,
  handlePrint: PropTypes.func,
  isLoading: PropTypes.bool,
  isUserList: PropTypes.bool,
  onIsUserListChange: PropTypes.func,
};

export default function InvoiceToolbar({
  handlePrint,
  users,
  filterStartDate,
  filterEndDate,
  filterUser,
  onFilterUser,
  onFilterStartDate,
  onFilterEndDate,
  handleRefresh,
  isLoading,
  reports,
  isUserList,
  onIsUserListChange,
}) {
  const [openPDF, setOpenPDF] = useState(false);

  const handleOpenPreview = () => {
    setOpenPDF(true);
  };

  const handleClosePreview = () => {
    setOpenPDF(false);
  };

  const { user } = useAuth();

  const { t } = useTranslation();

  return (
    <>
      <Stack mb={5} direction="row" justifyContent="flex-end" spacing={1.5}>
        <Button
          color="error"
          size="small"
          variant="contained"
          onClick={handlePrint}
        >
          {t("params.back")}
        </Button>

        <Button
          color="info"
          size="small"
          variant="contained"
          onClick={handleOpenPreview}
          endIcon={<Iconify icon={"eva:eye-fill"} />}
          sx={{ mx: 1 }}
        >
          {t("sideBar.OVERVIEW")}
        </Button>

        <PDFDownloadLink
          document={
            <InvoicePDF
              reports={reports}
              filterUser={filterUser}
              filterStartDate={filterStartDate}
              filterEndDate={filterEndDate}
              onFilterUser={onFilterUser}
              onFilterStartDate={onFilterStartDate}
              onFilterEndDate={onFilterEndDate}
              users={users}
              handleRefresh={handleRefresh}
              isLoading={isLoading}
              handlePrint={handlePrint}
              user={user}
              isUserList={isUserList}
              onIsUserListChange={onIsUserListChange}
            />
          }
          fileName={`${t("p_report.report_sale")} ${t("params.from")} ${fDate(
            filterStartDate
          )} ${t("params.to")} ${fDate(filterEndDate)}`}
          style={{ textDecoration: "none" }}
        >
          {({ loading }) => (
            <LoadingButton
              size="small"
              loading={loading}
              variant="contained"
              loadingPosition="end"
              endIcon={<Iconify icon={"eva:download-fill"} />}
            >
              {t("params.download")}
            </LoadingButton>
          )}
        </PDFDownloadLink>
      </Stack>

      <DialogAnimate fullScreen open={openPDF}>
        <Box sx={{ height: "100%", display: "flex", flexDirection: "column" }}>
          <DialogActions
            sx={{
              zIndex: 9,
              padding: "12px !important",
              boxShadow: (theme) => theme.customShadows.z8,
            }}
          >
            <Tooltip title="Close">
              <IconButton color="inherit" onClick={handleClosePreview}>
                <Iconify icon={"eva:close-fill"} />
              </IconButton>
            </Tooltip>
          </DialogActions>
          <Box sx={{ flexGrow: 1, height: "100%", overflow: "hidden" }}>
            <PDFViewer width="100%" height="100%" style={{ border: "none" }}>
              <InvoicePDF
                reports={reports}
                filterUser={filterUser}
                filterStartDate={filterStartDate}
                filterEndDate={filterEndDate}
                onFilterUser={onFilterUser}
                onFilterStartDate={onFilterStartDate}
                onFilterEndDate={onFilterEndDate}
                users={users}
                handleRefresh={handleRefresh}
                isLoading={isLoading}
                handlePrint={handlePrint}
                user={user}
                isUserList={isUserList}
                onIsUserListChange={onIsUserListChange}
              />
            </PDFViewer>
          </Box>
        </Box>
      </DialogAnimate>
    </>
  );
}
