import PropTypes from "prop-types";
import {
  Tooltip,
  Stack,
  TextField,
  MenuItem,
  Checkbox,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
// components
import Iconify from "../../../../components/Iconify";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

const INPUT_WIDTH = 160;

TicketTableToolbar.propTypes = {
  filterName: PropTypes.string,
  filterUser: PropTypes.string,
  filterMasterUser: PropTypes.string,
  filterEndDate: PropTypes.instanceOf(Date),
  filterStartDate: PropTypes.instanceOf(Date),
  onFilterName: PropTypes.func,
  onFilterEndDate: PropTypes.func,
  onFilterUser: PropTypes.func,
  onFilterMasterUser: PropTypes.func,
  onFilterStartDate: PropTypes.func,
  onIsUserListChange: PropTypes.func,
  users: PropTypes.array,
  masterusers: PropTypes.array,
  loadText: PropTypes.string,
  viewReportText: PropTypes.string,
  title: PropTypes.string,
  handleRefresh: PropTypes.func,
  handlePrint: PropTypes.func,
  isLoading: PropTypes.bool,
  isReportNotFound: PropTypes.bool,
  isUserList: PropTypes.bool,
};

export default function TicketTableToolbar({
  users,
  masterusers,
  filterStartDate,
  filterEndDate,
  filterUser,
  filterMasterUser,
  onFilterUser,
  onFilterMasterUser,
  onFilterStartDate,
  onFilterEndDate,
  loadText = "Load",
  viewReportText = "Preview",
  title = "Filter list",
  handleRefresh,
  handlePrint,
  isLoading,
  isReportNotFound,
  isUserList,
  onIsUserListChange
}) {
  const { t } = useTranslation();
  return (
    <Stack
      spacing={2}
      direction={{ xs: "column", md: "row" }}
      sx={{ py: 2.5, px: 3 }}
    >
      <Checkbox
        value={isUserList}
        onChange={onIsUserListChange}
      />
      <TextField
        fullWidth
        select
        label={t("field.user")}
        value={filterUser}
        onChange={onFilterUser}
        SelectProps={{
          MenuProps: {
            sx: { "& .MuiPaper-root": { maxHeight: 260 } },
          },
        }}
        sx={{
          textTransform: "capitalize",
          maxWidth: { md: INPUT_WIDTH },
        }}
      >
        {users?.map((usr) => (
          <MenuItem
            key={usr.uid}
            value={usr.uid}
            sx={{
              mx: 1,
              my: 0.5,
              borderRadius: 0.75,
              typography: "body2",
              textTransform: "capitalize",
            }}
          >
            {usr.full_name}
          </MenuItem>
        ))}
      </TextField>

      <TextField
        fullWidth
        select
        label={t("field.supervisor")}
        value={filterMasterUser}
        onChange={onFilterMasterUser}
        SelectProps={{
          MenuProps: {
            sx: { "& .MuiPaper-root": { maxHeight: 260 } },
          },
        }}
        sx={{
          textTransform: "capitalize",
          maxWidth: { md: INPUT_WIDTH },
        }}
      >
        {masterusers?.data?.map((usr) => (
          <MenuItem
            key={usr.uid}
            value={usr.uid}
            sx={{
              mx: 1,
              my: 0.5,
              borderRadius: 0.75,
              typography: "body2",
              textTransform: "capitalize",
            }}
          >
            {usr.full_name}
          </MenuItem>
        ))}
      </TextField>

      <DatePicker
        label={t("field.start_date")}
        value={filterStartDate}
        onChange={onFilterStartDate}
        renderInput={(params) => <TextField {...params} fullWidth />}
      />

      <DatePicker
        label={t("field.end_date")}
        value={filterEndDate}
        onChange={onFilterEndDate}
        renderInput={(params) => <TextField {...params} fullWidth />}
      />

      <Tooltip title={title}>
        <span>
          <LoadingButton
            startIcon={<Iconify icon={"ic:round-filter-list"} />}
            variant="contained"
            onClick={handleRefresh}
            loading={isLoading}
            size="large"
          >
            {loadText}
          </LoadingButton>
        </span>
      </Tooltip>
      {!isReportNotFound && (
        <Tooltip title="Effectuer Report">
          <span>
            <LoadingButton
              loading={isLoading}
              variant="contained"
              color="secondary"
              onClick={handlePrint}
              size="large"
            >
              {viewReportText}
            </LoadingButton>
          </span>
        </Tooltip>
      )}
    </Stack>
  );
}
