import React, { useState, useEffect } from "react";
import Report from "./ReportComp";
import Invoice from "./ReportInvoice";
import { getReports } from "../../../redux/slices/report";
import { getUsers } from "../../../redux/slices/generale";
import { getSupervisorsToSelect } from "../../../redux/slices/supervisor";
import { useDispatch, useSelector } from "../../../redux/store";
import { fDateOnly } from "../../../utils/formatTime";

export default function Reports() {
  const dispatch = useDispatch();
  const { reports, isLoading } = useSelector((state) => state.report);
  const { users } = useSelector((state) => state.generale);
  const { selectsupervisors } = useSelector((state) => state.supervisor);

  const [invoiceActive, setInvoiceActive] = useState(false);
  const [filterUser, setFilterUser] = useState("All");
  const [filterMasterUser, setFilterMasterUser] = useState("All");
  const [filterStartDate, setFilterStartDate] = useState(new Date());
  const [filterEndDate, setFilterEndDate] = useState(new Date());
  const [isUserList, setIsUserList] = useState(true);

  const handleRefresh = () => {
    dispatch(
      getReports(
        fDateOnly(filterStartDate, "yyyy-M-d"),
        fDateOnly(filterEndDate, "yyyy-M-d"),
        filterUser,
        filterMasterUser
      )
    );
    dispatch(getUsers());
    dispatch(getSupervisorsToSelect());
  };

  useEffect(() => {
    handleRefresh();
  }, [dispatch]);

  const handlePrint = () => {
    setInvoiceActive(!invoiceActive);
  };

  const onIsUserListChange = () => {
    setIsUserList(!isUserList);
  };

  const handleFilterUser = (event) => {
    setFilterUser(event.target.value);
  };
  const handleFilterMasterUser = (event) => {
    setFilterMasterUser(event.target.value);
  };

  return (
    <>
      {invoiceActive === false ? (
        <Report
          handlePrint={handlePrint}
          filterUser={filterUser}
          filterMasterUser={filterMasterUser}
          filterStartDate={filterStartDate}
          filterEndDate={filterEndDate}
          onFilterUser={handleFilterUser}
          onFilterMasterUser={handleFilterMasterUser}
          onFilterStartDate={(newValue) => {
            setFilterStartDate(newValue);
          }}
          onFilterEndDate={(newValue) => {
            setFilterEndDate(newValue);
          }}
          users={users}
          masterusers={selectsupervisors}
          handleRefresh={handleRefresh}
          isLoading={isLoading}
          reports={reports}
          isUserList={isUserList}
          onIsUserListChange={onIsUserListChange}
        />
      ) : (
        <Invoice
          handlePrint={handlePrint}
          filterUser={filterUser}
          filterMasterUser={filterMasterUser}
          filterStartDate={filterStartDate}
          filterEndDate={filterEndDate}
          onFilterUser={handleFilterUser}
          onFilterMasterUser={handleFilterMasterUser}
          onFilterStartDate={(newValue) => {
            setFilterStartDate(newValue);
          }}
          onFilterEndDate={(newValue) => {
            setFilterEndDate(newValue);
          }}
          users={users}
          masterusers={selectsupervisors}
          reports={reports}
          isUserList={isUserList}
          onIsUserListChange={onIsUserListChange}
        />
      )}
    </>
  );
}
