import PropTypes from "prop-types";
// material
import { Card, Container, Grid } from "@mui/material";
// redux
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// hooks
import useSettings from "../../../hooks/useSettings";
// components
import Page from "../../../components/Page";
import HeaderBreadcrumbs from "../../../components/HeaderBreadcrumbs";
import {
  ReportListToolbar,
  UsersRep,
  SupRep,
  Report,
} from "../../../sections/@dashboard/report/list";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

ReportComp.propTypes = {
  reports: PropTypes.array,
  filterUser: PropTypes.string,
  filterMasterUser: PropTypes.string,
  filterEndDate: PropTypes.instanceOf(Date),
  filterStartDate: PropTypes.instanceOf(Date),
  onFilterEndDate: PropTypes.func,
  onFilterUser: PropTypes.func,
  onFilterMasterUser: PropTypes.func,
  onFilterStartDate: PropTypes.func,
  users: PropTypes.array,
  masterusers: PropTypes.array,
  handleRefresh: PropTypes.func,
  handlePrint: PropTypes.func,
  isLoading: PropTypes.bool,
  isUserList: PropTypes.bool,
  onIsUserListChange: PropTypes.func,
};

export default function ReportComp({
  handlePrint,
  users,
  masterusers,
  filterStartDate,
  filterEndDate,
  filterUser,
  filterMasterUser,
  onFilterUser,
  onFilterMasterUser,
  onFilterStartDate,
  onFilterEndDate,
  handleRefresh,
  isLoading,
  reports,
  isUserList,
  onIsUserListChange,
}) {
  const { t } = useTranslation();
  const { themeStretch } = useSettings();
  const isReportNotFound = !reports || reports?.report?.counts === 0;
  return (
    <Page title={`Report: ${t("p_report.report_sale")}`}>
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={t("p_report.report_sale")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("p_report.report"),
              href: PATH_DASHBOARD.report.root,
            },
            { name: t("p_report.report_sale") },
          ]}
        />
        <Grid container spacing={3}>
          <Grid item xs={12} md={12}>
            <Card>
              <ReportListToolbar
                filterUser={filterUser}
                filterMasterUser={filterMasterUser}
                filterStartDate={filterStartDate}
                filterEndDate={filterEndDate}
                onFilterUser={onFilterUser}
                onFilterMasterUser={onFilterMasterUser}
                onFilterStartDate={onFilterStartDate}
                onFilterEndDate={onFilterEndDate}
                users={users}
                masterusers={masterusers}
                handleRefresh={handleRefresh}
                isLoading={isLoading}
                placeholder={t("params.search", {
                  obj: t("p_report.report"),
                })}
                loadText={t("params.load")}
                viewReportText={t("params.view_report")}
                title={t("params.filter_list")}
                handlePrint={handlePrint}
                isReportNotFound={isReportNotFound}
                isUserList={isUserList}
                onIsUserListChange={onIsUserListChange}
              />
            </Card>
          </Grid>
          <Grid item xs={12} md={12}>
            <Report report={reports?.report} />
          </Grid>
          {reports?.vendors?.length > 0 && isUserList && (
            <Grid item xs={12} md={12}>
              <UsersRep list={reports?.vendors} />
            </Grid>
          )}
          {reports?.supervisors?.length > 0 && (
            <Grid item xs={12} md={12}>
              <SupRep list={reports?.supervisors} />
            </Grid>
          )}
        </Grid>
      </Container>
    </Page>
  );
}
