import PropTypes from "prop-types";
// material
import { styled, useTheme } from "@mui/material/styles";
import {
  Box,
  Grid,
  Card,
  Table,
  Divider,
  TableRow,
  Container,
  TableBody,
  TableHead,
  TableCell,
  Typography,
  TableContainer,
} from "@mui/material";
// utils
import { fCurrency } from "../../../utils/formatNumber";
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// hooks
import useSettings from "../../../hooks/useSettings";
import useAuth from "../../../hooks/useAuth";
// components
import Page from "../../../components/Page";
import Label from "../../../components/Label";
import Scrollbar from "../../../components/Scrollbar";
import HeaderBreadcrumbs from "../../../components/HeaderBreadcrumbs";
import { InvoiceToolbar } from "../../../sections/@dashboard/report/invoice";
import { fDateTime, fDate } from "../../../utils/formatTime";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

const RowResultStyle = styled(TableRow)(({ theme }) => ({
  "& td": {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
}));

// ----------------------------------------------------------------------

ReportInvoice.propTypes = {
  reports: PropTypes.array,
  filterUser: PropTypes.string,
  filterEndDate: PropTypes.instanceOf(Date),
  filterStartDate: PropTypes.instanceOf(Date),
  onFilterEndDate: PropTypes.func,
  onFilterUser: PropTypes.func,
  onFilterStartDate: PropTypes.func,
  users: PropTypes.array,
  handleRefresh: PropTypes.func,
  handlePrint: PropTypes.func,
  isLoading: PropTypes.bool,
  isUserList: PropTypes.bool,
  onIsUserListChange: PropTypes.func,
};

export default function ReportInvoice({
  handlePrint,
  users,
  filterStartDate,
  filterEndDate,
  filterUser,
  onFilterUser,
  onFilterStartDate,
  onFilterEndDate,
  handleRefresh,
  isLoading,
  reports,
  isUserList,
  onIsUserListChange,
}) {
  const { t } = useTranslation();
  const { user } = useAuth();
  const { themeStretch } = useSettings();
  const theme = useTheme();

  const today = fDateTime(new Date());

  return (
    <Page title={`Report: ${t("p_report.report_sale")}`}>
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={t("p_report.report_sale")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("p_report.report"),
              href: PATH_DASHBOARD.report.root,
            },
            { name: t("p_report.report_sale") },
          ]}
        />

        <InvoiceToolbar
          reports={reports}
          filterUser={filterUser}
          filterStartDate={filterStartDate}
          filterEndDate={filterEndDate}
          onFilterUser={onFilterUser}
          onFilterStartDate={onFilterStartDate}
          onFilterEndDate={onFilterEndDate}
          users={users}
          handleRefresh={handleRefresh}
          isLoading={isLoading}
          handlePrint={handlePrint}
          isUserList={isUserList}
          onIsUserListChange={onIsUserListChange}
        />

        <Card sx={{ pt: 5, px: 5 }}>
          <Grid container>
            <Grid item xs={12} sm={6}>
              <Box sx={{ textAlign: { sm: "left" }, width: 120, height: 50 }}>
                <img
                  src="/static/logo/logo1.png"
                  alt="logo"
                  style={{ paddingLeft: 18, paddingRight: 18 }}
                />
              </Box>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Box sx={{ textAlign: { sm: "right" } }} />
            </Grid>
            <Grid item xs={12} sm={6}>
              <Box sx={{ textAlign: { sm: "left" } }}>
                <Typography variant="h6">Bling</Typography>
              </Box>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Box sx={{ textAlign: { sm: "right" } }}>
                <Typography variant="h6">{fDateTime(today)}</Typography>
              </Box>
            </Grid>
            <Grid item xs={12} sm={12}>
              <Box sx={{ textAlign: { sm: "center" } }}>
                <Typography variant="h3">
                  {t("p_report.report_sale")}
                </Typography>
              </Box>
            </Grid>

            <Grid item xs={12} sm={6}>
              <Typography
                paragraph
                variant="overline"
                sx={{ color: "text.disabled" }}
              >
                {t("params.from")}
              </Typography>
              <Typography variant="body2">
                {fDate(reports?.start_date)}
              </Typography>
            </Grid>

            <Grid item xs={12} sm={6}>
              <Typography
                paragraph
                variant="overline"
                sx={{ color: "text.disabled" }}
              >
                {t("params.to")}
              </Typography>
              <Typography variant="body2">
                {fDate(reports?.end_date)}
              </Typography>
            </Grid>
            <Grid item xs={12} sm={4}>
              <Typography paragraph variant="overline">
                {t("sideBar.vendor")}:{" "}
                {users?.find((e) => e.id === filterUser)?.full_name ||
                  `${t("params.all")}`}
              </Typography>
            </Grid>
          </Grid>
          {isUserList && (
            <>
              <Typography paragraph variant="overline">
                {t("sideBar.vendor")}
              </Typography>

              <Scrollbar>
                <TableContainer sx={{ minWidth: 960 }}>
                  <Table>
                    <TableHead
                      sx={{
                        borderBottom: (theme) =>
                          `solid 1px ${theme.palette.divider}`,
                        "& th": { backgroundColor: "transparent" },
                      }}
                    >
                      <TableRow>
                        <TableCell align="left">{t("field.user")}</TableCell>
                        <TableCell align="center">
                          {t("app.total_ticket")}
                        </TableCell>
                        <TableCell align="center">
                          {t("app.total_montant")}
                        </TableCell>
                        <TableCell align="center">
                          %{t("sideBar.vendor")}
                        </TableCell>
                      </TableRow>
                    </TableHead>

                    <TableBody>
                      {reports?.vendors?.map((row, index) => (
                        <TableRow
                          key={index}
                          sx={{
                            borderBottom: (theme) =>
                              `solid 1px ${theme.palette.divider}`,
                          }}
                        >
                          <TableCell>{row.full_name}</TableCell>
                          <TableCell align="center">{row.counts}</TableCell>
                          <TableCell align="center">
                            {fCurrency(row.montants)}
                          </TableCell>
                          <TableCell align="center">
                            {fCurrency(row.percents)}
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </Scrollbar>
            </>
          )}
          {reports?.supervisors?.length > 0 && (
            <>
              <br />
              <br />
              <Typography paragraph variant="overline">
                {t("sideBar.supervisor")}
              </Typography>

              <Scrollbar>
                <TableContainer sx={{ minWidth: 960 }}>
                  <Table>
                    <TableHead
                      sx={{
                        borderBottom: (theme) =>
                          `solid 1px ${theme.palette.divider}`,
                        "& th": { backgroundColor: "transparent" },
                      }}
                    >
                      <TableRow>
                        <TableCell align="left">
                          {t("sideBar.supervisor")}
                        </TableCell>
                        <TableCell align="center">
                          {t("app.total_montant")}
                        </TableCell>
                        <TableCell align="right">
                          {t("app.total_commission")}
                        </TableCell>
                      </TableRow>
                    </TableHead>

                    <TableBody>
                      {reports?.supervisors?.map((row, index) => (
                        <TableRow
                          key={index}
                          sx={{
                            borderBottom: (theme) =>
                              `solid 1px ${theme.palette.divider}`,
                          }}
                        >
                          <TableCell>{row.full_name}</TableCell>
                          <TableCell align="center">
                            {fCurrency(row.montants)}
                          </TableCell>
                          <TableCell align="right">
                            {fCurrency(row.percents)}
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </Scrollbar>
            </>
          )}

          <br />
          <Typography paragraph variant="overline">
            {t("field.total")}
          </Typography>

          <Scrollbar>
            <TableContainer sx={{ minWidth: 960 }}>
              <Table>
                <RowResultStyle>
                  {/* <TableCell colSpan={1} /> */}
                  <TableCell align="left">
                    <Typography variant="subtitle2">
                      {t("app.total_ticket")}:{" "}
                      {fCurrency(reports?.report?.counts)}
                    </Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">
                      {t("app.total_montant")}:{" "}
                      {fCurrency(reports?.report?.montants)}
                    </Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">
                      %{t("sideBar.vendor")}:{" "}
                      {fCurrency(reports?.report?.percent_vendor)}
                    </Typography>
                  </TableCell>
                  <TableCell align="left">
                    <Typography variant="subtitle2">
                      %{t("sideBar.supervisor")}:{" "}
                      {fCurrency(reports?.report?.percent_supervisor)}
                    </Typography>
                  </TableCell>
                </RowResultStyle>
              </Table>
            </TableContainer>
          </Scrollbar>

          <Divider sx={{ mt: 5 }} />

          <Grid container>
            <Grid item xs={12} md={9} sx={{ py: 3 }}>
              <Typography variant="subtitle2">{t("params.notes")}</Typography>
              <Typography variant="body2">
                {t("params.notices_report")}
              </Typography>
            </Grid>
          </Grid>
        </Card>
      </Container>
    </Page>
  );
}
